/* global window */
import Close from '@buzzguru/ui/assets/icons/clear-big';
import Settings from '@buzzguru/ui/assets/icons/settings';
import Button from '@lskjs/button';
import Modal from '@lskjs/modal';
import T from '@lskjs/t';
import PropTypes from 'prop-types';
import React, { useContext } from 'react';

import { ToolbarContext } from '../../../Toolbar';
import { Wrapper } from './RightContent.styles';

const RightContent = ({ app }) => {
  const ctx = useContext(ToolbarContext);
  const onClose = () => {
    ctx.triggerOpen();
  };
  const logout = () => {
    window?.__lskjs?.GM_setValue('authToken', null);
    console.log('authToken', window?.__lskjs?.GM_getValue('authToken'));
    app.redirect('/login');
  };
  return (
    <Wrapper>
      <Modal
        trigger={
          <Button
            style={{ width: 36, height: 36, borderRadius: 8, backgroundColor: '#4C4C56' }}
            view="empty"
            icon={<Settings size={18} style={{ color: '#ffffff' }} />}
            isRipple={false}
          />
        }
      >
        <Modal.Content style={{ backgroundColor: '#313137', borderRadius: 8 }}>
          <Button paint="common" block style={{ width: '100%', marginTop: 48 }} onClick={logout}>
            <T name="buttons.logout" />
          </Button>
        </Modal.Content>
      </Modal>
      <Button view="empty" icon={<Close size={18} style={{ color: '#ffffff' }} />} onClick={onClose} isRipple={false} />
    </Wrapper>
  );
};

RightContent.propTypes = {
  app: PropTypes.objectOf(Object).isRequired,
};

export default RightContent;
